html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Fira Code", monospace;
  background: #171717;
  color: white; }

button {
  background: white;
  border: none;
  padding: 0.75rem 3rem;
  font-size: 1rem;
  font-family: "Fira Code", monospace;
  font-weight: 400;
  margin: 0 0.5rem; }

input {
  min-width: 50%;
  margin: 0 0 1rem 1rem;
  background: transparent;
  color: white;
  border: none;
  border-bottom: 1px solid white;
  padding-bottom: 4px;
  font-size: 1rem;
  font-family: "Fira Code", monospace;
  font-weight: 300; }

label {
  font-weight: 600; }

.inactive {
  -webkit-filter: blur(2px);
          filter: blur(2px); }

.Timebox {
  border: 2px solid white;
  padding: 1rem;
  margin: 1rem;
  background: lightsteelblue; }

.TimeboxEditor {
  padding: 1rem;
  border: 2px solid white;
  text-align: right;
  margin: 1rem;
  background: lightsalmon; }

.TimeboxCreator {
  padding: 1rem;
  border: 2px solid white;
  text-align: right;
  margin: 1rem;
  background: lightseagreen; }

.CurrentTimebox {
  padding: 1rem;
  border: 2px solid white;
  text-align: center;
  margin: 1rem;
  background: tomato; }

.progress {
  height: 30px;
  width: 100%;
  border: 1px solid white;
  margin-bottom: 1rem;
  display: flex;
  align-items: center; }
  .progress__bar {
    height: 20px;
    margin: auto 4px;
    background: #5c5c5c; }
    .progress__bar--red {
      background: #d72121; }
    .progress__bar--blue {
      background: blue; }
    .progress__bar--green {
      background: green; }

.clock {
  color: white;
  font-weight: normal; }
  .clock__numbers {
    font-weight: bold;
    background: none; }
    .clock__numbers--colored:nth-of-type(1) {
      color: #d81159; }
    .clock__numbers--colored:nth-of-type(2) {
      color: #8f2d56; }
    .clock__numbers--colored:nth-of-type(3) {
      color: #218380; }
    .clock__numbers--colored:nth-of-type(4) {
      color: #fbb13c; }

